}
.primary-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
  box-sizing: border-box;
  padding: 5px 10px;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  overflow: hidden;
  text-overflow: ellipsis;
  border: none;
  outline: none;
  cursor: pointer;
}
.primary-btn:hover {
  color: rgba(255, 255, 255, 0.6);
  background-color: rgba(94, 170, 47, 0.6);
}
.primary-btn:focus {
  color: rgba(255, 255, 255, 0.6);
  background-color: rgba(94, 170, 47, 0.6);
  outline: none;
}
.primary-btn:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: rgba(94, 170, 47, 0.3);
}
.secondary-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px 0;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
  border: none;
  outline: none;
  cursor: pointer;
}
.secondary-btn:hover {
  color: rgba(0, 0, 0, 0.6);
}
.secondary-btn:focus {
  color: rgba(0, 0, 0, 0.6);
  outline: none;
}
.secondary-btn:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .secondary-btn {
    font-size: 20px;
    line-height: 20px;
  }
}
.third-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  box-sizing: border-box;
  min-height: 40px;
  padding: 10px;
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
