  background-color: rgba(235, 235, 235, 0.6);
}
.third-btn:focus {
  color: rgba(68, 68, 68, 0.6);
  background-color: rgba(235, 235, 235, 0.6);
  outline: none;
}
.third-btn:active {
  color: rgba(68, 68, 68, 0.3);
  background-color: rgba(235, 235, 235, 0.3);
}
.input-field {
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-align: left;
  text-decoration: none;
  text-transform: uppercase;
  color: #444444;
  background-color: transparent;
  border: 1px solid #e7e7e7;
  outline: none;
  padding: 10px;
}
.input-field:hover:not([type="radio"]):not([type="checkbox"]) {
  border: 1px solid #cdcdcd;
  outline: 1px solid #cdcdcd;
}
.input-field:focus:not([type="radio"]):not([type="checkbox"]) {
  border: 1px solid #444444;
  outline: 1px solid #444444;
}
.input-field:focus + .configuration-group__icon {
  stroke: #68b738;
}
.input-field:active:not([type="radio"]):not([type="checkbox"]) {
  border: 1px solid #444444;
  outline: 1px solid #444444;
}
.input-field:active + .configuration-group__icon {
  stroke: #68b738;
}
.input-field:focus:invalid {
  border: 1px solid #ff8282;
  outline: 1px solid #ff8282;
}
.input-field:focus:invalid + .configuration-group__icon {
  stroke: #ff8282;
}
.input-field::placeholder {
  font-family: "Oswald", "Times New Roman", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-align: left;
  text-decoration: none;
  text-transform: uppercase;
  color: #444444;
}
@media (min-width: 768px) {
  .input-field {
    font-size: 20px;
    line-height: 20px;
  }
}
.page {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
}
.page__main {
  flex: 1 1 auto;
}
.header {
  padding-top: 25px;
  padding-bottom: 10px;
  z-index: 50;
  position: relative;
  overflow: hidden;
}
.header__container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .header__container {
    max-width: 708px;
  }
}
@media (min-width: 1440px) {
  .header__container {
    max-width: 1220px;
  }
}
.header__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  align-self: center;
  justify-content: space-between;
  min-height: 40px;
  padding-top: 10px;
  position: relative;
}
.header__burger--opened,
.header__burger--closed {
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}
.header__burger--opened:hover,
.header__burger--closed:hover {
  opacity: 0.8;
}
.header__burger--opened:focus,
.header__burger--closed:focus {
  opacity: 0.8;
}
.header__burger--opened:active,
.header__burger--closed:active {
  opacity: 0.6;
}
.header__burger--opened {
  background-image: url("../img/logo/burger_open.svg");
  width: 24px;
  height: 24px;
}
.header__burger--closed {
  background-image: url("../img/logo/burger_close.svg");
  width: 18px;
  height: 18px;
}
.header__navigation--opened {
  display: block;
}
.header__navigation--closed {
  display: none;
}
@media (min-width: 768px) {
  .header {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .header__wrapper {
    min-height: 50px;
    justify-content: left;
    padding-top: 15px;
  }
  .header__burger {
    display: none;
  }
  .header__navigation--opened {
    display: block;
  }
  .header__navigation--closed {
    display: block;
  }
}
